import React, { useEffect } from 'react';
import { useNavigate } from 'react-router-dom';

// Funo para verificar o status de login
export function checkLoginStatus() {
  const isLoggedIn = localStorage.getItem('authToken') !== null;
  return isLoggedIn;
}

// Funo para deslogar o usurio
export function logout() {
  localStorage.removeItem('authToken');
  window.location.href = '/';
}

// Componente para redirecionar o usurio com base no status de login
export function LoginRedirect() {
  const navigate = useNavigate();
  navigate('/');
  useEffect(() => {
    if (checkLoginStatus()) {
      navigate('/');
    } else {
      navigate('/login');
    }
  }, [navigate]);

  return null;
}

// Componente para deslogar o usurio e redirecion-lo para a pgina inicial
export function LogoutRedirect() {
  const navigate = useNavigate();

  useEffect(() => {
    logout();
    navigate('/');
  }, [navigate]);

  return null;
}
